import { Component, Element, Host, Prop, Watch, h } from '@stencil/core';
import type { OMenu } from '../o-menu/o-menu';
import type { OMenuItem } from '../o-menu-item/o-menu-item';

@Component({
	tag: 'o-dropdown',
	styleUrl: 'o-dropdown.scss',
	shadow: true
})
export class ODropdown {
	private popup: HTMLOPopupElement;
	private panel: HTMLSlotElement;
	private trigger: HTMLSlotElement;
	private menu: OMenu;
	private menuItems: OMenuItem[];

	@Element() host: HTMLODropdownElement;

	@Prop({ mutable: true }) open: boolean = false;

	@Prop() placement:
		| 'top'
		| 'top-start'
		| 'top-end'
		| 'bottom'
		| 'bottom-start'
		| 'bottom-end'
		| 'right'
		| 'right-start'
		| 'right-end'
		| 'left'
		| 'left-start'
		| 'left-end' = 'bottom';

	@Prop() disabled: boolean = false;

	@Watch('open')
	async watchOpen() {
		if (this.disabled) {
			this.open = false;
			return;
		}

		if (this.open) {
			this.addListeners();
			this.panel.hidden = false;
			this.popup.active = true;

			const { keyframes, options } = this.getAnimation('dropdown.show');
			await Promise.resolve(this.animateTo(this.popup?.shadowRoot?.querySelector('div'), keyframes, options));
		} else {
			this.removeListeners();
			this.panel.hidden = true;
			this.popup.active = false;

			const { keyframes, options } = this.getAnimation('dropdown.hide');
			await Promise.resolve(this.animateTo(this.popup?.shadowRoot?.querySelector('div'), keyframes, options));
		}
	}

	focusOnTrigger() {
		const trigger = this.trigger.assignedElements?.({ flatten: true })[0] as HTMLElement | undefined;
		if (typeof trigger?.focus === 'function') {
			trigger.focus();
		}
	}

	getMenu() {
		return this.panel
			.assignedElements?.({ flatten: true })
			.find((el) => el.tagName.toLowerCase() === 'o-menu') as unknown as OMenu | undefined;
	}

	/* istanbul ignore next */
	addListeners() {
		this.panel.addEventListener('o-menu-select', this.handlePanelSelect);
		this.panel.addEventListener('keydown', this.handleKeyDown);
		document.addEventListener('keydown', this.handleDocumentKeyDown);
		document.addEventListener('mousedown', this.handleDocumentMouseDown);
	}

	/* istanbul ignore next */
	removeListeners() {
		this.panel.removeEventListener('o-menu-select', this.handlePanelSelect);
		this.panel.removeEventListener('keydown', this.handleKeyDown);
		document.removeEventListener('keydown', this.handleDocumentKeyDown);
		document.removeEventListener('mousedown', this.handleDocumentMouseDown);
	}

	async show() {
		if (this.open) return;

		this.open = true;
	}

	async hide() {
		if (!this.open) return;

		this.open = false;
	}

	/* istanbul ignore next */
	async handleContentSlotChange() {
		this.menu = this.getMenu();
		if (this.menu) {
			this.menuItems = await this.menu.getMenuItems();
		}

		// Apply drop shadow to slotted content
		const slottedContent = this.panel.assignedElements({ flatten: true })[0] as HTMLElement;
		if (slottedContent) {
			slottedContent.style.boxShadow = '0px 2px 4px 0px rgba(0, 0, 0, 0.25)';
			slottedContent.style.borderRadius = '8px';
		}
	}

	/* istanbul ignore next */
	handleTriggerClick = () => {
		if (this.open) {
			this.hide();
		} else {
			this.show();
			this.focusOnTrigger();
		}
	};

	/* istanbul ignore next */
	handleTriggerKeyDown = (event: KeyboardEvent) => {
		if ([' ', 'Enter'].includes(event.key)) {
			event.preventDefault();
			this.handleTriggerClick();
			return;
		}

		if (this.menu) {
			const firstMenuItem = this.menuItems[0];
			const lastMenuItem = this.menuItems[this.menuItems.length - 1];

			if (['ArrowUp', 'ArrowDown', 'Home', 'End'].includes(event.key)) {
				event.preventDefault();

				if (!this.open) () => this.show();

				if (this.menuItems.length > 0) {
					if (event.key === 'ArrowUp' || event.key === 'Home') {
						this.menu.setCurrentItem(firstMenuItem);
						firstMenuItem.setFocus();
					}

					if (event.key === 'ArrowDown' || event.key === 'End') {
						this.menu.setCurrentItem(lastMenuItem);
						lastMenuItem.setFocus();
					}
				}
			}
		}
	};

	/* istanbul ignore next */
	handlePanelSelect = () => {
		this.hide();
		this.focusOnTrigger();
	};

	/* istanbul ignore next */
	handleKeyDown = (event: KeyboardEvent) => {
		if (this.open && event.key === 'Escape') {
			event.stopPropagation();
			this.hide();
			this.focusOnTrigger();
		}
	};

	/* istanbul ignore next */
	handleDocumentKeyDown = (event: KeyboardEvent) => {
		if (event.key === 'Escape' && this.open) {
			event.stopPropagation();
			this.focusOnTrigger();
			this.hide();
			return;
		}

		if (event.key === 'Tab') {
			if (this.open && document.activeElement?.tagName.toLowerCase() === 'o-menu-item') {
				event.preventDefault();
				this.hide();
				this.focusOnTrigger();
				return;
			}
		}
	};

	/* istanbul ignore next */
	handleDocumentMouseDown = (event: MouseEvent) => {
		const path = event.composedPath();
		if (this.host && !path.includes(this.host)) this.hide();
	};

	getAnimation(animationName: string) {
		const animations = {
			'dropdown.show': {
				keyframes: [
					{ opacity: 0, scale: 0.9 },
					{ opacity: 1, scale: 1 }
				],
				options: { duration: 100, easing: 'ease' }
			},
			'dropdown.hide': {
				keyframes: [
					{ opacity: 1, scale: 1 },
					{ opacity: 0, scale: 0.9 }
				],
				options: { duration: 100, easing: 'ease' }
			}
		};

		return animations[animationName] || { keyframes: [], options: {} };
	}

	async animateTo(element: HTMLElement, keyframes: Keyframe[], options: KeyframeAnimationOptions) {
		if (element) {
			const animation = element.animate(keyframes, options);
			return new Promise<void>((resolve) => {
				animation.onfinish = () => resolve();
			});
		} else {
			return Promise.resolve();
		}
	}

	render() {
		return (
			<Host>
				<o-popup ref={(el) => (this.popup = el)} placement={this.placement}>
					<div slot="anchor" onClick={this.handleTriggerClick} onKeyDown={this.handleTriggerKeyDown}>
						<slot ref={(el) => (this.trigger = el as HTMLSlotElement)} name="trigger"></slot>
					</div>
					<div aria-hidden={this.open ? 'false' : 'true'} aria-labelledby="dropdown">
						<slot
							ref={(el) => (this.panel = el as HTMLSlotElement)}
							onSlotchange={() => this.handleContentSlotChange()}
						></slot>
					</div>
				</o-popup>
			</Host>
		);
	}
}
